import { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { User } from '../context/use-user';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';

export default function LoginForm() {

    const { updateUserName } = useContext(User);

    const navigate = useNavigate();

    const handleSubmit = async (values, { setSubmitting, resetForm }) => {

        const url = "https://script.google.com/macros/s/AKfycbzR7_MG6Tqi8Y7cdmsRGH66Ill0cPAsiBbfx7UAVtmC9K29prLCrY989dtXA5tV-VI6/exec?action=login&name="+values.name+"&password="+values.password;
        
        await fetch(url)
            .then(response => {
                console.log(response)
                console.log(response.status)
                if(!response.ok) throw new Error(response.status);
                else {
                    return response.text();
                }
            })
            .then((data) => { //data -   setUserData  , ,   return
                console.log({data});
                const regResult = JSON.parse(data);
                console.log(regResult);
                if(regResult.mystatus === 'Password is wrong') {
                    alert('Your password is invalid. Try to log in again.');
                }else if(regResult.mystatus === 'There is no user with this name') {
                    alert('There is no user with this name. Please register!');
                }else {
                    alert('Login successful!');
                    updateUserName(values.name);
                    setSubmitting(false);//isSubmitting -   , true ( ), false ( )
                    navigate(`/tasks`);
                    resetForm();// 
                }
            })
            .catch((error) => {
                alert(`Registration failed: ${error.message}`);
                console.log({error});
                setSubmitting(false);
            });       
    }

    const SignupSchema = Yup.object().shape({
        name: Yup.string()
        .min(3, 'Must be at least 3 characters!')
        .max(20, 'Must be 20 characters or less!')
        .matches(/^[a-zA-Z0-9]+$/, 'Only letters and numbers are allowed')//^   $   [a-zA-Z0-9]  ( ,  )
        .required('Required'),
        password: Yup.string()
        .min(8, 'Must be at least 8 characters!')
        .max(20, 'Must be 20 characters or less!')
        .matches(/^[a-zA-Z0-9]+$/, 'Only letters and numbers are allowed')
        .required('Required'),
    });

    return (
        <>
            <Formik
            initialValues={{
            name: '',
            password: '',
            }}
            validationSchema={SignupSchema}
            onSubmit={handleSubmit}
            >
                {({ isSubmitting }) => ( //isSubmitting -   , true ( ), false ( )
                //  className  , ,      ( , ,   ..)
                <Form>
                    <h1>Registration</h1>
                    <div className='input__container-form'>
                        <div>
                            <label htmlFor="name">Name:</label>
                        </div>
                        <Field name="name" />
                        <ErrorMessage name="name" render={msg => <p className="error__message-form">{msg}</p>} />
                    </div>
                    <div className='input__container-form'>
                        <div>
                            <label htmlFor="password">Password:</label>
                        </div>
                        <Field name="password" type="password" />
                        <ErrorMessage name="password" render={msg => <p className="error__message-form">{msg}</p>} />
                    </div>
                    <button type="submit" disabled={isSubmitting}>Submit</button>
                </Form>
                )}
            </Formik>
        </>
    )
}